body {
  text-align: center;
  background-color: #f0f2f5;
  margin: 0;
  padding: 20px;
  font-size: 46px;
}

h1 {
  color: #333;
  margin-bottom: 30px;
}

.gallery {
  flex-wrap: wrap;
  gap: 15px;
  margin: 0 auto;
  list-style: none;
  display: flex;
}

.gallery-item {
  border-radius: 20px;
  flex-basis: calc(25% - 11.25px);
  transition: all .25s;
  overflow: hidden;
}

.gallery-item:hover {
  transform: scale(1.05);
}

.gallery-img {
  object-fit: cover;
  width: 100%;
  height: 200px;
  display: block;
}

.btn-load-more {
  color: #efef04;
  cursor: pointer;
  background-color: #0828f8;
  border: none;
  border-radius: 9px;
  margin-top: 30px;
  margin-bottom: 50px;
  padding: 12px 30px;
  font-size: 16px;
  font-weight: bold;
  transition: all .4s;
}

.btn-load-more:hover {
  color: #0820f0;
  background-color: #d4f808;
}

.loading {
  color: #666;
  margin: 20px 0;
  font-size: 18px;
  display: none;
}
